.food {
  background-image: url("/static/foodBg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 71rem; }
  .food .container {
    width: 65%;
    margin: auto;
    padding-top: 7rem; }
    .food .container .wrapper {
      display: flex;
      flex-wrap: wrap; }
      .food .container .wrapper .text__container {
        flex: 50%;
        max-width: 50%;
        text-align: center;
        padding-top: 6rem; }
        .food .container .wrapper .text__container .text__container__title {
          font-family: "Yeseva One", cursive;
          font-size: 3.6rem;
          color: #dab33e; }
        .food .container .wrapper .text__container .text__container__desc {
          font-size: 1.6rem;
          color: #dab33e;
          color: black;
          margin: 3rem auto 5rem;
          width: 90%; }
      .food .container .wrapper .img__container {
        flex: 50%;
        max-width: 50%; }
        .food .container .wrapper .img__container img {
          width: 100%; }

@media screen and (max-width: 1200px) {
  .food .container {
    width: 80%; } }

@media screen and (max-width: 1200px) {
  .food .container .wrapper .text__container .text__container__title {
    font-size: 2.5rem; }
  .food .container .wrapper .text__container .text__container__desc {
    font-size: 1.4rem;
    margin: 2rem auto 2rem; }
  .food .container .wrapper .text__container .text__container__signature {
    width: 70%; } }

@media screen and (max-width: 768px) {
  .food .container {
    width: 80%; }
    .food .container .wrapper .text__container {
      flex: 100%;
      max-width: 100%; }
      .food .container .wrapper .text__container .text__container__signature {
        width: 50%; }
    .food .container .wrapper .img__container {
      flex: 100%;
      max-width: 100%;
      text-align: center; }
      .food .container .wrapper .img__container img {
        width: 70%; } }
