.header {
  transition: all 0.5s ease-in; }
  .header .nav {
    width: 90%;
    margin-left: 10rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 1rem;
    position: fixed;
    top: 0;
    left: 0; }
    .header .nav .btn__collapse {
      display: none;
      font-size: 2.5rem;
      color: white; }
    .header .nav .nav__brand {
      flex: 10%;
      max-width: 10%; }
    .header .nav .nav__main {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      margin-left: 10%; }
      .header .nav .nav__main .nav__list {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row; }
        .header .nav .nav__main .nav__list .nav__item {
          padding-left: 2.6rem;
          padding-right: 2.6rem;
          position: relative; }
          .header .nav .nav__main .nav__list .nav__item .nav__link {
            font-family: "Gentium Book Basic", serif;
            font-size: 2.1rem;
            color: white;
            text-decoration: none; }
            .header .nav .nav__main .nav__list .nav__item .nav__link:hover {
              color: #dab33e; }
      .header .nav .nav__main .nav__social .nav__twitter,
      .header .nav .nav__main .nav__social .nav__youtube,
      .header .nav .nav__main .nav__social .nav__facebook {
        font-size: 2.1rem;
        color: #dab33e;
        text-decoration: none;
        margin: 1rem; }
        .header .nav .nav__main .nav__social .nav__twitter:hover,
        .header .nav .nav__main .nav__social .nav__youtube:hover,
        .header .nav .nav__main .nav__social .nav__facebook:hover {
          color: white; }
      .header .nav .nav__main .lang__vi, .header .nav .nav__main .lang__en {
        font-size: 3rem;
        color: white; }

.show {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 13rem;
  position: fixed;
  top: 0;
  left: 0;
  transition: all 0.5s ease-in; }

@media screen and (max-width: 993px) {
  .header {
    margin-left: 0; }
    .header .nav .btn__collapse {
      display: block;
      position: fixed;
      top: 2.5rem;
      right: 2.5rem;
      width: 5rem;
      height: 5rem;
      text-align: center;
      line-height: 5rem;
      color: white;
      cursor: pointer;
      z-index: 10; }
    .header .nav .nav__bg {
      position: fixed;
      top: 2.5rem;
      right: 2.5rem;
      width: 5rem;
      height: 5rem;
      border-radius: 5rem;
      text-align: center;
      background-color: rgba(0, 0, 0, 0.5);
      z-index: 8;
      transition: transform 0.25s linear; }
    .header .nav .nav__main {
      position: fixed;
      top: 0;
      left: 0;
      width: 60%;
      height: 100vh;
      z-index: 9;
      transform: translateX(-100%);
      transition: transform 0.25s linear;
      background-color: #dab33e;
      margin-left: 0; }
      .header .nav .nav__main .nav__list {
        flex-direction: column;
        padding: 3rem; }
        .header .nav .nav__main .nav__list .nav__item {
          margin: 2rem; }
      .header .nav .nav__main .nav__social {
        margin-left: 5rem; }
        .header .nav .nav__main .nav__social .nav__twitter,
        .header .nav .nav__main .nav__social .nav__youtube,
        .header .nav .nav__main .nav__social .nav__facebook {
          color: white; }
    .header .nav .inp__collapse:checked + .nav__bg {
      transform: scale(80); }
    .header .nav .inp__collapse:checked ~ .nav__main {
      transform: translateX(0); } }
